            ></v-img>
          </v-carousel-item>
        </v-carousel>
      </v-col>
      <v-col cols="12" md="4">
        <v-card>
          <v-card-title class="text-h6">
            <v-icon start color="primary" icon="mdi-timeline-text"></v-icon>
            
          </v-card-title>
          <v-card-text>
            <v-select
              v-model="currentTimeline"
