            >
              
            </v-btn>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    
    <!--  -->
    <v-timeline 
      v-if="timelineView === 'vertical'"
      align="start" 
      justify="center"
      line-thickness="2"
      line-color="primary"
    >
      <v-timeline-item
        v-for="(event, i) in currentTimelineData"
        :key="i"
        :dot-color="event.color || 'primary'"
        size="small"
        fill-dot
      >
        <template v-slot:opposite>
          <div
            :class="`pt-1 headline font-weight-bold text-${event.color || 'primary'}`"
            v-text="event.year || event.date"
          ></div>
        </template>
        <div>
          <h2
            :class="`mt-n1 headline font-weight-light mb-2 text-${event.color || 'primary'}`"
          >
            {{ event.title }}
          </h2>
          <div class="text-body-1">
            {{ event.content }}
          </div>
          <v-chip-group v-if="event.tags && event.tags.length > 0" class="mt-2">
            <v-chip
              v-for="(tag, tagIndex) in event.tags"
              :key="tagIndex"
              size="small"
              color="grey-lighten-3"
            >
              {{ tag }}
            </v-chip>
          </v-chip-group>
        </div>
      </v-timeline-item>
    </v-timeline>
    
    <!--  -->
    <div v-else-if="timelineView === 'horizontal'" class="horizontal-timeline">
      <div class="timeline-scroll-container">
        <div class="timeline-track">
          <div 
            v-for="(event, i) in currentTimelineData" 
            :key="i"
            class="timeline-event"
            :class="{'active': selectedEvent === i}"
            @click="selectedEvent = i"
          >
            <div class="timeline-date">{{ event.year || event.date }}</div>
            <div 
              class="timeline-dot" 
              :style="{backgroundColor: getColor(event.color || 'primary')}"
            ></div>
            <div class="timeline-title">{{ event.title }}</div>
          </div>
        </div>
      </div>
      
      <v-card v-if="selectedEvent !== null" class="mt-4">
        <v-card-title :class="`text-${currentTimelineData[selectedEvent].color || 'primary'}`">
          {{ currentTimelineData[selectedEvent].title }}
        </v-card-title>
        <v-card-subtitle>{{ currentTimelineData[selectedEvent].year || currentTimelineData[selectedEvent].date }}</v-card-subtitle>
        <v-card-text>{{ currentTimelineData[selectedEvent].content }}</v-card-text>
        <v-card-actions v-if="currentTimelineData[selectedEvent].tags && currentTimelineData[selectedEvent].tags.length > 0">
          <v-chip
            v-for="(tag, tagIndex) in currentTimelineData[selectedEvent].tags"
            :key="tagIndex"
            size="small"
            color="grey-lighten-3"
            class="mr-2"
          >
            {{ tag }}
          </v-chip>
        </v-card-actions>
      </v-card>
    </div>
    
    <!--  -->
    <v-dialog v-model="showImportDialog" max-width="600">
      <v-card>
        <v-card-title class="text-h5"></v-card-title>
        <v-card-text>
          <v-tabs v-model="importTab">
            <v-tab value="file"></v-tab>
            <v-tab value="text"></v-tab>
          </v-tabs>
          
          <v-window v-model="importTab">
            <v-window-item value="file">
              <v-file-input
                v-model="importFile"
                accept=".json, .txt"
                label="JSON"
                prepend-icon="mdi-file-document"
                variant="outlined"
                class="mt-4"
                show-size
                @change="onFileSelected"
              ></v-file-input>
              
              <div class="text-caption text-grey">
                JSON
                <a href="#" @click.prevent="downloadSampleFile"></a>
              </div>
            </v-window-item>
            
            <v-window-item value="text">
              <v-textarea
                v-model="importText"
                label="JSON"
                variant="outlined"
                hint="JSON"
                persistent-hint
                rows="10"
                class="mt-4"
              ></v-textarea>
            </v-window-item>
          </v-window>
          
          <v-text-field
            v-model="newTimelineName"
            label=""
            variant="outlined"
            class="mt-4"
            density="comfortable"
          ></v-text-field>
          
          <v-select
            v-model="newTimelineIcon"
            :items="availableIcons"
            label=""
            variant="outlined"
            item-props
            density="comfortable"
          >
            <template v-slot:item="{ item, props }">
              <v-list-item v-bind="props">
                <template v-slot:prepend>
                  <v-icon :icon="item.raw"></v-icon>
                </template>
                <v-list-item-title>{{ item.raw }}</v-list-item-title>
              </v-list-item>
            </template>
            <template v-slot:selection="{ item }">
              <v-icon :icon="item.value" class="mr-2"></v-icon>
              {{ item.value }}
            </template>
          </v-select>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" variant="text" @click="showImportDialog = false"></v-btn>
          <v-btn 
            color="primary" 
            variant="tonal" 
            @click="importTimelineData"
            :disabled="!canImport"
          >
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    
    <!--  -->
    <v-dialog v-model="showAddEntryDialog" max-width="600">
      <v-card>
        <v-card-title class="text-h5"></v-card-title>
        <v-card-text>
          <v-text-field
            v-model="newEvent.title"
            label=""
            variant="outlined"
            density="comfortable"
            class="mb-2"
          ></v-text-field>
          
          <v-text-field
            v-model="newEvent.date"
            label="/"
            variant="outlined"
            density="comfortable"
            class="mb-2"
          ></v-text-field>
          
          <v-textarea
            v-model="newEvent.content"
            label=""
            variant="outlined"
            density="comfortable"
            class="mb-2"
            rows="3"
          ></v-textarea>
          
          <v-select
            v-model="newEvent.color"
            :items="availableColors"
            label=""
            variant="outlined"
            density="comfortable"
            class="mb-2"
          >
            <template v-slot:item="{ item, props }">
              <v-list-item v-bind="props">
                <template v-slot:prepend>
                  <v-avatar :color="item.raw" size="24"></v-avatar>
                </template>
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item>
            </template>
            <template v-slot:selection="{ item }">
              <v-avatar :color="item.value" size="24" class="mr-2"></v-avatar>
              {{ item.title }}
            </template>
          </v-select>
          
          <v-combobox
            v-model="newEvent.tags"
            label=""
            variant="outlined"
            density="comfortable"
            chips
            multiple
            clearable
          ></v-combobox>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" variant="text" @click="showAddEntryDialog = false"></v-btn>
          <v-btn 
            color="primary" 
            variant="tonal" 
            @click="addNewEvent"
            :disabled="!newEvent.title || !newEvent.date"
          >
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    
    <!--  -->
    <v-dialog v-model="showDiaryImportDialog" max-width="600">
      <v-card>
        <v-card-title class="text-h5"></v-card-title>
        <v-card-text>
          <v-alert
            v-if="diaryImportError"
            type="error"
            variant="tonal"
            density="comfortable"
            class="mb-3"
          >
            {{ diaryImportError }}
          </v-alert>
          
          <v-alert
            v-if="diaryImportSuccess"
            type="success"
            variant="tonal"
            density="comfortable"
            class="mb-3"
          >
            {{ diaryImportSuccess }}
          </v-alert>
          
          <v-form @submit.prevent="importDiaryToTimeline">
            <v-file-input
              v-model="diaryImportFile"
              accept=".json"
              label=""
              prepend-icon="mdi-file-document"
              variant="outlined"
              class="mb-4"
              hint="JSON"
              persistent-hint
              @change="handleDiaryFileSelect"
            ></v-file-input>
            
            <v-row>
              <v-col cols="8">
                <v-text-field
                  v-model="diaryTimelineName"
                  label=""
                  variant="outlined"
                  hint=""
                  persistent-hint
                ></v-text-field>
              </v-col>
              
              <v-col cols="4">
                <v-select
                  v-model="diaryTimelineIcon"
                  :items="availableIcons"
                  label=""
                  variant="outlined"
                >
                  <template v-slot:item="{ item, props }">
                    <v-list-item v-bind="props">
                      <template v-slot:prepend>
                        <v-icon :icon="item.raw"></v-icon>
                      </template>
                      <v-list-item-title>{{ item.raw }}</v-list-item-title>
                    </v-list-item>
                  </template>
                  <template v-slot:selection="{ item }">
                    <v-icon :icon="item.value" class="mr-2"></v-icon>
                  </template>
                </v-select>
              </v-col>
            </v-row>
            
            <div class="text-right mt-2">
              <v-btn
                variant="text"
                color="secondary"
                prepend-icon="mdi-download"
                @click="downloadSampleDiaryData"
              >
                
              </v-btn>
            </div>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            variant="tonal"
            @click="importDiaryToTimeline"
            :disabled="!diaryImportResult"
            :loading="diaryImportLoading"
          >
            
          </v-btn>
          <v-btn
            color="grey"
            variant="text"
            @click="showDiaryImportDialog = false"
          >
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  name: "About",
  data() {
    return {
      years: [
        {
          color: 'cyan',
          year: '2021.9',
          title: '',
          content: '~'
        },
        {
          color: 'green',
          year: '2022.6',
          title: '',
          content:
            '~'
        },
        {
          color: 'pink',
          year: '2023.6',
          title: 'web',
          content:
            'rubycss'
        },
        {
          color: 'amber',
          year: '2024.6',
          title: '',
          content:
            ''
        },
        {
          color: 'orange',
          year: '2024.8',
          title: '',
          content:
            'djangovuetifyweb app~'
        }
      ],
      images: [],
      // 
      timelineView: 'vertical', // 
      selectedEvent: null, // 
      currentTimeline: 'personal', // ID
      availableTimelines: [
        { 
          id: 'personal', 
          name: '', 
          description: '',
          icon: 'mdi-account-school', 
          data: [] // created
        },
        { 
          id: 'tech', 
          name: '', 
          description: '',
          icon: 'mdi-code-tags', 
          data: [
            {
              title: 'HTMLCSS',
              date: '2022.3',
              content: '',
              color: 'blue',
              tags: ['HTML', 'CSS', '']
            },
            {
              title: 'JavaScript',
              date: '2022.5',
              content: 'JavaScript',
              color: 'yellow-darken-2',
              tags: ['JavaScript', '']
            },
            {
              title: 'Vue',
              date: '2023.2',
              content: 'Vue.js',
              color: 'green',
              tags: ['Vue', '']
            },
            {
              title: 'Vuetify',
              date: '2023.8',
              content: 'VuetifyUI',
              color: 'indigo',
              tags: ['Vuetify', 'UI']
            }
          ]
        },
        {
          id: 'diary',
          name: '',
          description: '',
          icon: 'mdi-notebook-outline',
          data: []
        }
      ],
      // 
      showImportDialog: false,
      importTab: 'file',
      importFile: null,
      importText: '',
      newTimelineName: '',
      newTimelineIcon: 'mdi-timeline-clock',
      // 
      showAddEntryDialog: false,
      newEvent: {
        title: '',
        date: '',
        content: '',
        color: 'primary',
        tags: []
      },
      // 
      availableColors: [
        { title: '', value: 'primary' },
        { title: '', value: 'blue' },
        { title: '', value: 'green' },
        { title: '', value: 'red' },
        { title: '', value: 'orange' },
        { title: '', value: 'purple' },
        { title: '', value: 'cyan' },
        { title: '', value: 'pink' },
        { title: '', value: 'amber' },
        { title: '', value: 'brown' }
      ],
      availableIcons: [
        'mdi-timeline-clock',
        'mdi-timeline-text',
        'mdi-timeline-outline',
        'mdi-account-school',
        'mdi-code-tags',
        'mdi-heart',
        'mdi-star',
        'mdi-book-open',
        'mdi-rocket-launch',
        'mdi-map-marker',
        'mdi-briefcase',
        'mdi-lightbulb'
      ],
      // 
      showDiaryImportDialog: false,
      diaryImportFile: null,
      diaryImportResult: null,
      diaryTimelineName: '',
      diaryTimelineIcon: 'mdi-notebook-outline',
      diaryImportLoading: false,
      diaryImportError: '',
      diaryImportSuccess: '',
    };
  },
  computed: {
    // 
    currentTimelineData() {
      if (!this.currentTimeline) return [];
      return this.availableTimelines.find(t => t.id === this.currentTimeline).data;
    },
    // 
    canImport() {
      if (this.importTab === 'file' && !this.importFile) return false;
      if (this.importTab === 'text' && !this.importText.trim()) return false;
      return !!this.newTimelineName.trim();
    },
    // 
    selectedTimeline() {
      // availableTimelines
      const timeline = this.availableTimelines.find(t => t.id === this.currentTimeline);
      return timeline || this.availableTimelines[0];
    }
  },
  created() {
    this.loadImages();
    // 
    this.availableTimelines[0].data = this.years;
    // 
    this.currentTimeline = 'personal';
  },
  methods: {
    loadImages() {
      try {
        const requireContext = require.context('./', false, /\.(png|jpe?g|svg)$/);
        this.images = requireContext.keys().map(file => ({
          src: requireContext(file)
        }));
      } catch (error) {
        console.error(':', error);
        // 
        this.images = [
          { src: 'https://picsum.photos/id/11/500/300' },
          { src: 'https://picsum.photos/id/12/500/300' },
          { src: 'https://picsum.photos/id/13/500/300' }
        ];
      }
    },
    // 
    getColor(colorName) {
      const colorMap = {
        'primary': '#1976D2',
        'blue': '#2196F3',
        'green': '#4CAF50',
        'red': '#F44336',
        'orange': '#FF9800',
        'purple': '#9C27B0',
        'cyan': '#00BCD4',
        'pink': '#E91E63',
        'amber': '#FFC107',
        'brown': '#795548'
      };
      return colorMap[colorName] || colorMap['primary'];
    },
    // 
    onFileSelected(file) {
      if (!file) return;
      
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const content = e.target.result;
          // JSON
          const jsonData = JSON.parse(content);
          // 
          this.importText = JSON.stringify(jsonData, null, 2);
          // 
          const fileName = file.name.replace(/\.(json|txt)$/, '');
          if (!this.newTimelineName && fileName) {
            this.newTimelineName = fileName;
          }
        } catch (error) {
          console.error('JSON:', error);
          alert('JSON');
        }
      };
      reader.readAsText(file);
    },
    // 
    downloadSampleFile() {
      const sampleData = [
        {
          title: '1',
          date: '2022.1',
          content: '1',
          color: 'blue',
          tags: ['1', '2']
        },
        {
          title: '2',
          date: '2022.6',
          content: '2',
          color: 'green',
          tags: ['3']
        },
        {
          title: '3',
          date: '2023.1',
          content: '3',
          color: 'red',
          tags: []
        }
      ];
      
      const dataStr = JSON.stringify(sampleData, null, 2);
      const dataUri = 'data:application/json;charset=utf-8,'+ encodeURIComponent(dataStr);
      
      const exportFileDefaultName = '.json';
      
      const linkElement = document.createElement('a');
      linkElement.setAttribute('href', dataUri);
      linkElement.setAttribute('download', exportFileDefaultName);
      linkElement.click();
    },
    // 
    importTimelineData() {
      try {
        let timelineData;
        
        if (this.importTab === 'file' && this.importFile) {
          // onFileSelectedimportText
          timelineData = JSON.parse(this.importText);
        } else if (this.importTab === 'text' && this.importText) {
          timelineData = JSON.parse(this.importText);
        } else {
          alert('');
          return;
        }
        
        // 
        if (!Array.isArray(timelineData)) {
          alert('');
          return;
        }
        
        // 
        const newTimeline = {
          id: 'timeline_' + Date.now(),
          name: this.newTimelineName,
          description: `${this.newTimelineName}`,
          icon: this.newTimelineIcon || 'mdi-timeline-clock',
          data: timelineData
        };
        
        // 
        this.availableTimelines.push(newTimeline);
        
        // 
        this.currentTimeline = newTimeline.id;
        
        // 
        this.showImportDialog = false;
        this.importFile = null;
        this.importText = '';
        this.newTimelineName = '';
        
        // 
        alert('');
      } catch (error) {
        console.error(':', error);
        alert('');
      }
    },
    // 
    addNewEvent() {
      if (!this.newEvent.title || !this.newEvent.date) {
        alert('');
        return;
      }
      
      // 
      const eventToAdd = { ...this.newEvent };
      
      // 
      const timeline = this.availableTimelines.find(t => t.id === this.currentTimeline);
      timeline.data.push(eventToAdd);
      
      // 
      timeline.data.sort((a, b) => {
        const dateA = a.date || a.year || '';
        const dateB = b.date || b.year || '';
        return dateA.localeCompare(dateB);
      });
      
      // 
      this.showAddEntryDialog = false;
      this.newEvent = {
        title: '',
        date: '',
        content: '',
        color: 'primary',
        tags: []
      };
    },
    // 
    exportCurrentTimeline() {
      if (!this.selectedTimeline || !this.currentTimelineData.length) {
        alert('');
        return;
      }
      
      // 
      const exportData = [...this.currentTimelineData];
      
      // JSON
      const dataStr = JSON.stringify(exportData, null, 2);
      const dataUri = 'data:application/json;charset=utf-8,'+ encodeURIComponent(dataStr);
      
      // 
      const exportFileDefaultName = `${this.selectedTimeline.name}_.json`;
      
      // 
      const linkElement = document.createElement('a');
      linkElement.setAttribute('href', dataUri);
      linkElement.setAttribute('download', exportFileDefaultName);
      linkElement.click();
    },
    // 
    openDiaryImportDialog() {
      this.showDiaryImportDialog = true;
      this.diaryImportFile = null;
      this.diaryImportResult = null;
      this.diaryImportError = '';
      this.diaryImportSuccess = '';
      this.diaryTimelineName = '';
      this.diaryTimelineIcon = 'mdi-notebook-outline';
    },
    
    handleDiaryFileSelect(e) {
      const file = e.target.files[0];
      if (!file) return;
      
      if (file.name.endsWith('.json')) {
        const reader = new FileReader();
        reader.onload = (event) => {
          try {
            const jsonData = JSON.parse(event.target.result);
            if (Array.isArray(jsonData)) {
              this.diaryImportResult = jsonData;
              this.diaryImportError = '';
              this.diaryImportSuccess = ` ${jsonData.length} `;
            } else {
              this.diaryImportError = '';
              this.diaryImportSuccess = '';
            }
          } catch (error) {
            this.diaryImportError = ': ' + error.message;
            this.diaryImportSuccess = '';
          }
        };
        reader.readAsText(file);
      } else {
        this.diaryImportError = 'JSON';
        this.diaryImportSuccess = '';
      }
    },
    
    importDiaryToTimeline() {
      if (!this.diaryImportResult || !Array.isArray(this.diaryImportResult)) {
        this.diaryImportError = '';
        return;
      }
      
      // 
      let diaryTimeline = this.availableTimelines.find(t => t.id === 'diary');
      
      if (!diaryTimeline) {
        diaryTimeline = {
          id: 'diary',
          name: this.diaryTimelineName,
          description: '',
          icon: this.diaryTimelineIcon,
          data: []
        };
        this.availableTimelines.push(diaryTimeline);
      } else {
        // 
        diaryTimeline.name = this.diaryTimelineName;
        diaryTimeline.icon = this.diaryTimelineIcon;
      }
      
      // 
      const timelineEvents = this.diaryImportResult.map(entry => {
        //  (YYYY.MM.DD -> YYYY.MM.DD)
        const date = entry.date || '';
        
        return {
          date: date,
          title: entry.title || `: ${date}`,
          content: entry.content || '',
          color: entry.color || 'blue',
          tags: entry.tags || []
        };
      });
      
      // 
      diaryTimeline.data = timelineEvents;
      
      // 
      this.currentTimeline = diaryTimeline.id;
      
      // 
      this.diaryImportSuccess = ` ${timelineEvents.length} `;
      setTimeout(() => {
        this.showDiaryImportDialog = false;
      }, 1500);
    },
    
    // 
    downloadSampleDiaryData() {
      const sampleData = [
        {
          "date": "2023.10.15",
          "title": ": 2023.10.15",
          "content": "Python\"\"",
          "color": "blue",
          "tags": ["", "", ""]
        },
        {
          "date": "2023.10.16",
          "title": ": 2023.10.16",
          "content": "bug",
          "color": "green",
          "tags": ["", "", ""]
        }
      ];
      
      const dataStr = JSON.stringify(sampleData, null, 2);
      const dataUri = `data:application/json;charset=utf-8,${encodeURIComponent(dataStr)}`;
      
      const linkElement = document.createElement('a');
      linkElement.setAttribute('href', dataUri);
      linkElement.setAttribute('download', 'diary_sample.json');
      linkElement.click();
    },
  }
};
</script>

